interface PostProps {
  avatar: string;
  username: string;
  time: string;
  content: string;
  image?: string; // Dấu ? nghĩa là có thể không có
  likes: number;
  comments: number;
  reposts: number;
  saves: number;
}
function Post({avatar, username, time, content, image, likes, comments, reposts, saves}: PostProps) {
  return (
    <div className="bg-white p-4 rounded-xl shadow-md w-full">
      <div className="flex items-center space-x-3">
        <img src={avatar} alt="avatar" className="w-10 h-10 rounded-full" />
        <div>
          <p className="font-semibold">{username}</p>
          <p className="text-sm text-gray-500">{time}</p>
        </div>
      </div>
      <p className="mt-2">{content}</p>
      {image && <img src={image} alt="post" className="mt-2 rounded-lg" />}
      <div className="flex justify-between mt-2 text-gray-500">
        <ActionButton icon={<Heart size={18} />} count={likes} />
        <ActionButton icon={<MessageCircle size={18} />} count={comments} />
        <ActionButton icon={<Repeat size={18} />} count={reposts} />
        <ActionButton icon={<Bookmark size={18} />} count={saves} />
      </div>
    </div>
  );
}

function ActionButton({ icon, count }: { icon: ReactNode; count: number }) {
  return (
    <button className="flex items-center space-x-1 hover:text-black">
      {icon}
      <span>{count}</span>
    </button>
  );
}
